import React from 'react';
import {Container,Row,Col} from 'react-bootstrap';
const Footer = ()=>{
    return(
        <footer>
            <Container>
                <Row>
                    <Col className="text-center py-3">
                        Copyrigth &copy; ProShop2021
                    </Col>
                </Row>
            </Container>
            Footer
        </footer>
    )
}

export default Footer;


